<template>
  <div class="add">
    <el-card>
      <!-- <el-alert title="" center type="info" :closable="false"> </el-alert> -->
      <el-steps class="mt-5" align-center :space="200" :active="activeIndex - 0" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" label-position="top">
        <el-tabs v-model="activeIndex" class="mt-5" tab-position="left" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane name="0" label="">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="editForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input-number :precision="2" :step="0.1" v-model="editForm.goods_price"></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input-number :precision="2" :step="0.5" v-model="editForm.goods_weight"></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input-number v-model="editForm.goods_number"></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                class="w-100"
                v-model="editForm.goods_cat"
                :options="catelist"
                :props="cascaderProps"
                clearable
                @change="handleCateChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane name="1" label="">
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox size="mini" border :label="subItem" v-for="(subItem, i) in item.attr_list" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane name="2" label="">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox size="mini" border :label="subItem" v-for="(subItem, i) in item.attr_list" :key="i"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane name="3" label="">
            <el-upload
              :headers="headers"
              :action="uploadUrl"
              :before-upload="beforeUpload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png/bmp/gif2M</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane name="4" label="">
            <quill-editor ref="myQuillEditor" v-model="editForm.goods_introduce" />
            <div class="text-right mt-3">
              <el-button @click="edit" type="primary" size="small"></el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane name="6" label="">
            <img class="add-success" src="@/assets/add-good-success.svg" alt="" />
            <p class="text-center mt-3">
               <strong>{{ countdown }}</strong> 
            </p>
            <p class="text-center"><router-link to="/goods"></router-link></p>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <el-dialog title="" :visible.sync="previewDialogVisible" width="50%">
      <img class="w-100" :src="previewPath" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import _ from 'lodash'
export default {
  name: 'Add',
  props: {
    id: {
      type: String,
      required: true
    }
  },
  components: {
    quillEditor
  },
  data() {
    return {
      activeIndex: '0',
      editForm: {
        goods_cat: []
      },
      editFormRules: {
        goods_name: [{ required: true, message: '', trigger: 'blur' }],
        goods_price: [{ required: true, message: '', trigger: 'blur' }],
        goods_weight: [{ required: true, message: '', trigger: 'blur' }],
        goods_number: [{ required: true, message: '', trigger: 'blur' }],
        goods_cat: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      catelist: [],
      cascaderProps: {
        label: 'cat_name',
        value: 'cat_id',
        chidlren: 'children'
      },

      // 
      uploadUrl: 'https://www.liulongbin.top:8888/api/private/v1/upload',
      headers: {
        Authorization: sessionStorage.token
      },
      // 
      previewPath: '',
      // 
      previewDialogVisible: false,

      // 
      editSuccess: false,
      // 
      countdown: 3,
      // 
      // fileList: [],
      manyTableData: [],
      onlyTableData: []
    }
  },
  created() {
    this.getCateList()
    this.getGoodInfo()
  },
  computed: {
    cateId() {
      if (this.editForm.goods_cat.length === 3) {
        return this.editForm.goods_cat[2]
      }
      return null
    }
  },
  // tab
  beforeTabLeave(activeName, oldActiveName) {
    if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
      this.$message({
        type: 'error',
        message: ''
      })
      return false
    }
    // 
    if (activeName === '6' && !this.editSuccess) {
      return false
    }
    if (oldActiveName === '6') return false
  },
  // 

  async tabClicked() {
    console.log('')
  },

  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.catelist = res.data
    },

    // 
    async getManyTableData(selected) {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: {
          sel: 'many'
        }
      })

      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }

      // 
      res.data.forEach(item => {
        item.attr_list = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        const index = selected.findIndex(selectedItem => selectedItem.attr_id === item.attr_id)
        if (index > -1) {
          item.attr_vals = selected[index].attr_value.length === 0 ? [] : selected[index].attr_value.split(' ')
        } else {
          item.attr_vals = []
        }
      })
      this.manyTableData = res.data
      console.log('manyTableData')
      console.log(this.manyTableData)
    },
    // 
    async getOnlyTableData(selected) {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: {
          sel: 'only'
        }
      })

      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }

      // 
      res.data.forEach(item => {
        item.attr_list = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        const index = selected.findIndex(selectedItem => selectedItem.attr_id === item.attr_id)
        if (index > -1) {
          item.attr_vals = selected[index].attr_value.length === 0 ? [] : selected[index].attr_value.split(' ')
        } else {
          item.attr_vals = []
        }
      })
      this.onlyTableData = res.data
      console.log('onlyTableData')
      console.log(this.onlyTableData)
    },

    // 
    handleCateChange() {
      // console.log(this.editForm.goods_cat)
      if (this.editForm.goods_cat.length !== 3) {
        this.editForm.goods_cat = []
      }
    },
    // tab
    beforeTabLeave(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.editForm.goods_cat.length !== 3) {
        this.$message({
          type: 'error',
          message: ''
        })
        return false
      }
      // 
      if (activeName === '6' && !this.editSuccess) {
        return false
      }
      if (oldActiveName === '6') return false
    },
    // 
    async tabClicked() {},
    // 
    handlePreview(file) {
      if (!file) return
      this.previewPath = file.response.data.url
      this.previewDialogVisible = true
    },
    // 
    handleRemove(file) {
      // file response 
      if (file.response) {
        const filePath = file.response.data.tmp_path
        const i = this.addForm.pics.findIndex(x => x.pic === filePath)
        this.addForm.pics.splice(i, 1)
      }
    },
    // 
    beforeUpload(file) {
      const isSupport = ['image/jpeg', 'image/png', 'image/bmp', 'image/gif'].indexOf(file.type) > -1
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isSupport) {
        this.$message.error(' JPG/PNG/BMP/GIF !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isSupport && isLt2M
    },
    // 
    handleSuccess(res) {
      this.addForm.pics.push({
        pic: res.data.tmp_path
      })
    },
    // 
    edit() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }

        const form = _.cloneDeep(this.editForm)
        form.goods_cat = form.goods_cat.join(',')
        form.attrs = []
        // /
        this.manyTableData.forEach(item => {
          form.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          })
        })
        this.onlyTableData.forEach(item => {
          form.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          })
        })

        // console.log(form)
        const { data: res } = await this.$http.put(`goods/${this.id}`, form)
        if (res.meta.status !== 200) {
          return this.$message({
            type: 'error',
            message: res.meta.msg
          })
        }
        this.$message({
          type: 'success',
          message: ''
        })
        this.editSuccess = true
        this.activeIndex = '6'
        const timer = setInterval(() => {
          this.countdown--
          if (this.countdown === 0) {
            clearInterval(timer)
            this.$router.push('/goods')
          }
        }, 1000)
      })
    },
    // 
    async getGoodInfo() {
      const { data: res } = await this.$http.get(`goods/${this.id}`)

      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }

      res.data.goods_cat = res.data.goods_cat.split(',').map(item => parseInt(item))

      this.editForm = res.data
      this.fileList = []
      this.editForm.pics = []

      this.getManyTableData(res.data.attrs.filter(attr => attr.attr_sel === 'many'))
      this.getOnlyTableData(res.data.attrs.filter(attr => attr.attr_sel === 'only'))

      // this.fileList = this.editForm.pics.map(pic => {
      //   return {
      //     name: pic.pics_sma_url.substring(pic.pics_sma_url.lastIndexOf('/') + 1),
      //     url: pic.pics_sma_url
      //   }
      // })

      console.log('')
      console.log(this.editForm)
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/ .el-steps .el-step__title {
  font-size: 1.3rem;
}
/deep/ .el-checkbox {
  margin: 0 5px 0 0 !important;
}
/deep/ .ql-editor {
  min-height: 260px;
}
