<template>
  <div class="roles">
    <el-card>
      <div class="text-right">
        <el-button type="primary" size="medium" @click="addDialogVisible = true"></el-button>
      </div>
      <el-table :data="rolelist" border class="mt-4">
        <!--  -->
        <el-table-column type="expand">
          <template #default="{row}">
            <el-row v-for="item1 in row.children" :key="item1.id" class="rights-row py-2 d-flex ai-center">
              <!--  -->
              <el-col :span="5" class="d-flex ai-center">
                <el-tag class="m-2" closable @close="removeRightById(row, item1)"> {{ item1.authName }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <!--  -->
                <el-row v-for="item2 in item1.children" :key="item2.id" class="rights-sub-row d-flex ai-center">
                  <el-col :span="6" class="d-flex ai-center">
                    <el-tag type="success" class="m-2" closable @close="removeRightById(row, item2)">{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!--  -->
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      class="m-2"
                      :key="item3.id"
                      closable
                      @close="removeRightById(row, item3)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" fixed="right" min-width="180" align="center">
          <template #default="{row}">
            <!-- enterable="false"  -->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="showEditDialog(row.id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="removeRoleById(row)" size="mini" type="danger" icon="el-icon-delete"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="showSetRightDialog(row)" size="mini" type="warning" icon="el-icon-setting"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" label-position="right">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false" size="medium"> </el-button>
        <el-button type="primary" @click="addRole" size="medium"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" label-position="right">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" size="medium"> </el-button>
        <el-button type="primary" @click="editRoleInfo" size="medium"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog @close="setRightDialogClosed" title="" :visible.sync="setRightDialogVisible" width="50%">
      <!-- show-checkbox  node-key default-expand-all default-checked-keys  key  -->
      <el-tree
        ref="treeRef"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defaultKeys"
        :data="rightslist"
        :props="treeProps"
      ></el-tree>

      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false" size="medium"> </el-button>
        <el-button type="primary" size="medium" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Roles',
  data() {
    return {
      rolelist: [],
      editForm: {},
      editDialogVisible: false,
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      addDialogVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ]
      },
      // 
      setRightDialogVisible: false,
      // 
      rightslist: [],
      // 
      treeProps: {
        label: 'authName', // 
        children: 'children' // 
      },
      //  id 
      defaultKeys: [],
      //  id
      roleId: ''
    }
  },

  methods: {
    // 
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')

      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.rolelist = res.data
      // console.log(this.rolelist)
    },
    // 
    async showEditDialog(id) {
      // id
      const { data: res } = await this.$http.get(`roles/${id}`)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.editForm = res.data
      this.editDialogVisible = true
      // console.log(this.editForm)
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editRoleInfo() {
      this.$refs.editFormRef.validate(async valid => {
        // console.log(this.editForm)
        if (!valid) return false
        const { data: res } = await this.$http.put(`roles/${this.editForm.roleId}`, {
          roleName: this.editForm.roleName,
          roleDesc: this.editForm.roleDesc
        })
        if (res.meta.status !== 200) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }
        this.editDialogVisible = false
        this.getRolesList()
        this.$message({
          type: 'success',
          message: ''
        })
      })
    },
    //  id 
    async removeRoleById(row) {
      // console.log(row)
      const confirmResult = await this.$confirm(` <strong>${row.roleName}</strong> , ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        dangerouslyUseHTMLString: true
      }).catch(err => err)
      // console.log(confirmResult)
      if (confirmResult === 'cancel') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      const { data: res } = await this.$http.delete(`roles/${row.id}`)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.$message({
        type: 'success',
        message: ''
      })
      this.getRolesList()
    },

    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addRole() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return false
        const { data: res } = await this.$http.post('roles', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }
        this.$message({
          type: 'success',
          message: ''
        })
        // 
        this.addDialogVisible = false
        // 
        this.getRolesList()
      })
    },
    //  id 
    async removeRightById(row, right) {
      const confirmResult = await this.$confirm(
        `<strong> ${row.roleName} </strong><strong> ${right.authName} </strong>`,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          dangerouslyUseHTMLString: true
        }
      ).catch(err => err)

      if (confirmResult === 'cancel') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      const { data: res } = await this.$http.delete(`roles/${row.id}/rights/${right.id}`)

      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.$message({
        type: 'success',
        message: ''
      })
      // 
      row.children = res.data
    },
    // 
    async showSetRightDialog(role) {
      this.roleId = role.id
      this.getLeafKeys(role, this.defaultKeys)
      this.setRightDialogVisible = true
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.rightslist = res.data
      // console.log(this.rightslist)
    },
    //  id
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setRightDialogClosed() {
      this.defaultKeys = []
    },
    // 
    async allotRights() {
      const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
      // console.log(keys)
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: idStr
      })
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.$message({
        type: 'success',
        message: ''
      })
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  },
  created() {
    this.getRolesList()
  }
}
</script>

<style lang="scss" scoped>
.roles {
  .rights-row {
    border-bottom: 1px solid #eee;
    &:first-child {
      border-top: 1px solid #eee;
    }
  }
  .rights-sub-row {
    border-bottom: 1px solid #eee;
    &:last-child {
      border-bottom: none;
    }
  }
}
</style>
