<template>
  <div class="list">
    <el-card>
      <el-row>
        <el-col :span="12">
          <el-input v-model="queryInfo.query" clearable placeholder="" size="medium" @clear="search" @keyup.enter.native="search">
            <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="12" class="text-right">
          <el-button @click="goAddPage" size="medium" type="primary"></el-button>
        </el-col>
      </el-row>

      <el-table class="mt-5" :data="goodslist" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="goods_name"></el-table-column>
        <el-table-column label="" prop="goods_price"></el-table-column>
        <el-table-column label="" prop="goods_weight"></el-table-column>
        <el-table-column label="" prop="add_time">
          <template #default="{row}">{{ row.add_time | dateFormat }}</template>
        </el-table-column>
        <el-table-column label="" fixed="right" min-width="180" align="center">
          <template #default="{row}">
            <!-- enterable="false"  -->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="goEditPage(row)" size="mini" type="primary" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="removeGoodById(row)" size="mini" type="danger" icon="el-icon-delete"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <div class="d-flex mt-4">
        <el-pagination
          background
          class="ml-auto"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'List',
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: parseInt(this.$route.query.pagenum) || 1,
        pagesize: parseInt(this.$route.query.pagesize) || 10
      },
      // 
      goodslist: [],
      total: 0,
      lastQuery: this.$route.query.query || ''
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }

      // console.log(res.data)
      this.goodslist = res.data.goods
      this.total = res.data.total
    },
    //  pagesize 
    handleSizeChange(newVal) {
      // console.log(newVal)
      this.queryInfo.pagesize = newVal
      this.getGoodsList()
      this.$router.push({ path: 'goods', query: this.queryInfo })
    },
    //  pagenum 
    handleCurrentChange(newVal) {
      this.queryInfo.pagenum = newVal
      this.getGoodsList()
      this.$router.push({ path: 'goods', query: this.queryInfo })
    },
    // 
    search() {
      if (this.lastQuery !== this.queryInfo.query) {
        this.queryInfo.pagenum = 1
        this.$router.push({ path: 'goods', query: this.queryInfo })
        this.getGoodsList()
        this.lastQuery = this.queryInfo.query
      }
    },
    //  id 
    async removeGoodById(row) {
      const confirmResult = await this.$confirm(` <strong>${row.goods_name}</strong> , ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        dangerouslyUseHTMLString: true
      }).catch(err => err)
      // console.log(confirmResult)
      if (confirmResult === 'cancel') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      const { data: res } = await this.$http.delete(`goods/${row.goods_id}`)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.$message({
        type: 'success',
        message: ''
      })
      this.getGoodsList()
    },

    goAddPage() {
      this.$router.push('/goods/add')
    },
    goEditPage(row) {
      console.log(row)
      this.$router.push(`goods/edit/${row.goods_id}`)
    }
  }
}
</script>

<style lang="scss" scoped>
.list {
}
</style>
