<template>
  <div class="cate">
    <el-card>
      <!--  -->
      <div class="text-right">
        <el-button type="primary" size="medium" @click="showAddCateDialog"></el-button>
      </div>
      <!--  -->
      <!-- selection-type   expand-type  show-index  index-text  -->
      <tree-table
        :selection-type="false"
        :expand-type="false"
        class="mt-4 fs-md"
        :data="catelist"
        :columns="columns"
        index-text="#"
        :show-index="true"
        border
      >
        <!--  -->
        <template slot="isok" slot-scope="{ row }">
          <i style="color: red;" class="el-icon-error" v-if="row.cat_deleted"></i>
          <i style="color: lightgreen;" class="el-icon-success" v-else></i>
        </template>
        <template slot="order" slot-scope="{ row }">
          <el-tag size="mini" v-if="row.cat_level === 0"></el-tag>
          <el-tag type="success" size="mini" v-else-if="row.cat_level === 1"></el-tag>
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <template slot="opt" slot-scope="{ row }">
          <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
            <el-button @click="showEditDialog(row.cat_id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
            <el-button @click="removeCateById(row)" size="mini" type="danger" icon="el-icon-delete"></el-button>
          </el-tooltip>
        </template>
      </tree-table>

      <!--  -->

      <div class="d-flex mt-4">
        <el-pagination
          class="ml-auto"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[3, 5, 10, 15]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- props  clearable   change-on-select -->
          <el-cascader
            class="w-100"
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false" size="medium"> </el-button>
        <el-button @click="addCate" type="primary" size="medium"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed" ref="edit">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" size="medium"> </el-button>
        <el-button @click="editCate()" type="primary" size="medium"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import TreeTable from 'vue-table-with-tree-grid'
export default {
  name: 'Categories',
  components: {
    TreeTable
  },
  data() {
    return {
      catelist: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: parseInt(this.$route.query.pagenum) || 1, // 
        pagesize: parseInt(this.$route.query.pagesize) || 5 // 
      },
      // 
      total: 0,
      //  tree-table 
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isok'
        },
        {
          label: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'opt',
          minWidth: '125px'
        }
      ],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        cat_name: '',
        //  id 0
        cat_pid: 0,
        //  0
        cat_level: 0
      },
      // 
      addFormRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        expandTrigger: 'hover', // 
        label: 'cat_name', // 
        value: 'cat_id', // 
        children: 'children' // 
      },
      //  value 
      selectedKeys: [],
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // id
      cateId: ''
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.catelist = res.data.result
      this.total = res.data.total
    },
    //  pagesize 
    handleSizeChange(newVal) {
      // console.log(newVal)
      this.queryInfo.pagesize = newVal
      this.getCateList()
      this.$router.push({ path: 'categories', query: this.queryInfo })
    },
    //  pagenum 
    handleCurrentChange(newVal) {
      this.queryInfo.pagenum = newVal
      this.getCateList()
      this.$router.push({ path: 'categories', query: this.queryInfo })
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
      this.selectedKeys = []
      this.addForm.cat_level = 0
      this.addForm.cat_pid = 0
    },

    // 
    showAddCateDialog() {
      this.getParentCateList()
      this.addDialogVisible = true
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      // console.log(res.data)
      this.parentCateList = res.data
    },
    // 
    parentCateChanged() {
      if (this.selectedKeys.length > 0) {
        //  id id
        this.addForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 
        this.addForm.cat_level = this.selectedKeys.length
      } else {
        this.addForm.cat_pid = 0
        this.addForm.cat_level = 0
      }
    },
    // 
    async addCate() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return false
        const { data: res } = await this.$http.post('categories', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }
        this.$message({
          type: 'success',
          message: ''
        })
        // 
        this.getCateList()
        // 
        this.addDialogVisible = false
      })
    },
    // 
    editDialogClosed() {
      this.cateId = ''
      this.$refs.editFormRef.resetFields()
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get(`categories/${id}`)
      this.editForm = res.data
      this.cateId = id
      this.editDialogVisible = true
    },
    // 
    editCate(id) {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return false
        const { data: res } = await this.$http.put(`categories/${this.cateId}`, {
          cat_name: this.editForm.cat_name
        })
        if (res.meta.status !== 200) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }
        this.editDialogVisible = false
        this.getCateList()
        this.$message({
          type: 'success',
          message: ''
        })
      })
    },

    // 

    async removeCateById(row) {
      const confirmResult = await this.$confirm(` <strong>${row.cat_name}</strong> , ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        dangerouslyUseHTMLString: true
      }).catch(err => err)

      if (confirmResult === 'cancel') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      const { data: res } = await this.$http.delete(`categories/${row.cat_id}`)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.$message({
        type: 'success',
        message: ''
      })
      this.getCateList()
    }
  }
}
</script>

