<template>
  <div class="order">
    <el-card>
      <el-row>
        <el-col :span="12">
          <el-input v-model="queryInfo.query" clearable placeholder="" size="medium" @clear="search">
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <el-table class="mt-5" :data="orderlist" border style="width: 100%">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="order_number" label=""> </el-table-column>
        <el-table-column prop="order_price" label=""> </el-table-column>
        <el-table-column prop="pay_status" label="">
          <template slot-scope="{ row }">
            <el-tag size="medium" v-if="row.pay_status === '1'" type="success"></el-tag>
            <el-tag size="medium" v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""> </el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="{ row }">
            {{ row.create_time | dateFormat }}
          </template>
        </el-table-column>

        <el-table-column label="" fixed="right" min-width="180" align="center">
          <template #default="{row}">
            <!-- enterable="false"  -->
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="showEditDialog(row.id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button @click="showProgressDialog" size="mini" type="success" icon="el-icon-location"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <div class="d-flex mt-4">
        <el-pagination
          background
          class="ml-auto"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader class="w-100" v-model="editForm.address1" :options="cityData" :props="cascaderProps" clearable></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" size="medium"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false" size="medium"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressDialogVisible" width="50%">
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'
export default {
  name: 'Order',
  data() {
    return {
      queryInfo: {
        query: this.$route.query.query || '', // 
        pagenum: parseInt(this.$route.query.pagenum) || 1, // 
        pagesize: parseInt(this.$route.query.pagesize) || 10 // 
      },
      // 
      total: 0,
      // 
      orderlist: [],
      lastQuery: this.$route.query.query || '',
      // 
      editDialogVisible: false,
      editForm: {
        address1: [],
        address2: ''
      },
      editFormRules: {
        address1: [{ required: true, message: '/', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      cityData,
      cascaderProps: {
        expandTrigger: 'hover', // 
        label: 'label', // 
        value: 'value', // 
        children: 'children' // 
      },
      // 
      progressDialogVisible: false,
      // 
      progressInfo: {}
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.total = res.data.total
      this.orderlist = res.data.goods
    },
    // 
    search() {
      if (this.lastQuery !== this.queryInfo.query) {
        this.queryInfo.pagenum = 1
        this.$router.push({ path: 'orders', query: this.queryInfo })
        this.getOrderList()
        this.lastQuery = this.queryInfo.query
      }
    },
    //  pagesize 
    handleSizeChange(newVal) {
      // console.log(newVal)
      this.queryInfo.pagesize = newVal
      this.getOrderList()
      this.$router.push({ path: 'orders', query: this.queryInfo })
    },
    //  pagenum 
    handleCurrentChange(newVal) {
      this.queryInfo.pagenum = newVal
      this.getOrderList()
      this.$router.push({ path: 'orders', query: this.queryInfo })
    },
    // 
    async showEditDialog(id) {
      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    async showProgressDialog() {
      const { data: res } = await this.$http.get('kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.progressInfo = res.data
      // console.log(this.progressInfo)
      this.progressDialogVisible = true
    }
  }
}
</script>

<style lang="scss" scoped>
.order {
}
</style>
