<template>
  <div class="params">
    <el-card>
      <el-alert show-icon :closable="false" title="" type="warning"> </el-alert>

      <div class="mt-4">
        <!--  -->
        <el-form label-width="100px" label-position="left">
          <el-form-item label="">
            <el-cascader size="medium" v-model="selectedKeys" :options="cateList" :props="cateProps" @change="handleChange"></el-cascader>
          </el-form-item>
        </el-form>
        <!-- tab  -->

        <el-tabs class="mt-3" v-model="activeName" @tab-click="handleTabClick">
          <!--  -->
          <el-tab-pane label="" name="many">
            <div class="text-right">
              <el-button @click="addDialogVisible = true" type="primary" size="mini" :disabled="isBtnDisabled">
                
              </el-button>
              <el-table class="mt-3" :data="manyTableData" border>
                <!--  -->
                <el-table-column type="expand">
                  <template slot-scope="{ row }">
                    <el-tag @close="handleTagClosed(i, row)" class="mr-3 mb-3" closable v-for="(item, i) in row.attr_vals" :key="i">
                      {{ item }}</el-tag
                    >

                    <el-input
                      v-if="row.inputVisible"
                      v-model="row.inputValue"
                      ref="saveTagInput"
                      size="small"
                      @keyup.enter.native="handleInputConfirm(row)"
                      @blur="handleInputConfirm(row)"
                      class="input-new-tag"
                    >
                    </el-input>
                    <el-button v-else size="small" @click="showInputVisible(row)">+ </el-button>
                  </template>
                </el-table-column>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="" prop="attr_name"></el-table-column>
                <el-table-column label="" min-width="180" align="center">
                  <template #default="{row}">
                    <!-- enterable="false"  -->
                    <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                      <el-button @click="showEditDialog(row.attr_id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                      <el-button @click="removeParams(row)" size="mini" type="danger" icon="el-icon-delete"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="only">
            <div class="text-right">
              <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true">
                
              </el-button>
              <el-table class="mt-3" :data="onlyTableData" border>
                <!--  -->
                <el-table-column type="expand">
                  <template slot-scope="{ row }">
                    <el-tag @close="handleTagClosed(i, row)" class="mr-3 mb-3" closable v-for="(item, i) in row.attr_vals" :key="i">
                      {{ item }}</el-tag
                    >

                    <el-input
                      v-if="row.inputVisible"
                      v-model="row.inputValue"
                      ref="saveTagInput"
                      size="small"
                      @keyup.enter.native="handleInputConfirm(row)"
                      @blur="handleInputConfirm(row)"
                      class="input-new-tag"
                    >
                    </el-input>
                    <el-button v-else size="small" @click="showInputVisible(row)">+ </el-button>
                  </template>
                </el-table-column>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="" prop="attr_name"></el-table-column>
                <el-table-column label="" min-width="180" align="center">
                  <template #default="{row}">
                    <!-- enterable="false"  -->
                    <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                      <el-button @click="showEditDialog(row.attr_id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
                      <el-button @click="removeParams(row)" size="mini" type="danger" icon="el-icon-delete"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-card>

    <!-- / -->
    <el-dialog :title="'' + titleText" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" label-position="left">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false" size="medium"> </el-button>
        <el-button @click="addParams" type="primary" size="medium"> </el-button>
      </span>
    </el-dialog>
    <!-- / -->
    <el-dialog :title="'' + titleText" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" label-position="left">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false" size="medium"> </el-button>
        <el-button @click="editParams" type="primary" size="medium"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Params',
  data() {
    return {
      // 
      cateList: [],
      // 
      cateProps: { expandTrigger: 'hover', value: 'cat_id', label: 'cat_name', children: 'children' },
      // 
      selectedKeys: [],
      // 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // /
      addForm: {
        attr_name: ''
      },
      // 
      addFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // /
      editDialogVisible: false,
      editForm: {
        attr_name: ''
      },
      // 
      editFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      inputVisible: false,
      // 
      inputValue: ''
    }
  },
  computed: {
    // 
    isBtnDisabled() {
      return this.selectedKeys.length !== 3
    },
    //  id
    catId() {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    titleText() {
      return this.activeName === 'many' ? '' : ''
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.cateList = res.data
      console.log(this.cateList)
    },
    // 
    async handleChange() {
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        //  table
        this.manyTableData = []
        this.onlyTableData = []
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.getParamsData()
    },
    // tab 
    handleTabClick() {
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        return false
      }
      this.getParamsData()
    },
    // 
    async getParamsData() {
      // id
      const { data: res } = await this.$http.get(`categories/${this.catId}/attributes`, {
        params: {
          sel: this.activeName
        }
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }

      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputValue = ''
        // 
        item.inputVisible = false
      })
      console.log(res.data)
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // /
    addParams() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return false
        }

        const { data: res } = await this.$http.post(`categories/${this.catId}/attributes`, {
          attr_name: this.addForm.attr_name,
          attr_sel: this.activeName
        })

        if (res.meta.status !== 201) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }
        this.$message.success('' + this.titleText + '')
        this.getParamsData()
        this.addDialogVisible = false
      })
    },

    // /
    async showEditDialog(attrId) {
      const { data: res } = await this.$http.get(`categories/${this.catId}/attributes/${attrId}`, {
        params: {
          attr_sel: this.activeName
        }
      })
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // /
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editParams() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return false

        const { data: res } = await this.$http.put(`categories/${this.catId}/attributes/${this.editForm.attr_id}`, {
          attr_name: this.editForm.attr_name,
          attr_sel: this.activeName,
          attr_vals: this.editForm.attr_vals
        })
        if (res.meta.status !== 200) {
          return this.$message({
            type: 'error',
            message: ''
          })
        }
        this.$message({
          type: 'success',
          message: ''
        })
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    //  /
    async removeParams(row) {
      const confirmResult = await this.$confirm(`${this.titleText} <strong>${row.attr_name}</strong> , ?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        dangerouslyUseHTMLString: true
      }).catch(err => err)
      // console.log(confirmResult)
      if (confirmResult === 'cancel') {
        return this.$message({
          type: 'info',
          message: ''
        })
      }

      const { data: res } = await this.$http.delete(`categories/${this.catId}/attributes/${row.attr_id}`)
      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: `${this.titleText}`
        })
      }
      this.$message({
        type: 'success',
        message: `${this.titleText}`
      })
      this.getParamsData()
    },
    //  / 
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length !== 0) {
        // 
        row.attr_vals.push(row.inputValue.trim())
        this.saveAttrVals(row)
      }
      row.inputValue = ''
      row.inputVisible = false
    },
    //  / 
    async saveAttrVals(row) {
      const { data: res } = await this.$http.put(`categories/${this.catId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })

      if (res.meta.status !== 200) {
        return this.$message({
          type: 'error',
          message: ''
        })
      }
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // 
    showInputVisible(row) {
      row.inputVisible = true
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    //  / 
    handleTagClosed(i, row) {
      row.attr_vals.splice(i, 1)
      this.saveAttrVals(row)
    }
  }
}
</script>

<style lang="scss" scoped>
.params {
  .input-new-tag {
    width: 90px;
  }
}
</style>
